import React, { Component } from 'react';

import LogoOBlack from '../../img/logos/LogoOBlack.png'

import Instagram from '../../img/socialIcons/instagram.svg'
import Facebook from '../../img/socialIcons/facebook.svg'
import Youtube from '../../img/socialIcons/youtube.svg'
import Soundcloud from '../../img/socialIcons/soundcloud.svg'

function scrollWin() {
  window.scrollTo(0, 0);
}

class Splash extends Component {

  render() {
    scrollWin();
    return (
      <div className="splashWrapper">
        <div className='overlay'></div>
        <img className="logo" src={LogoOBlack} alt="Facebook" />
        <h1>Coming Soon</h1>
        <div>
          <h3>Stay connected:</h3>
          <div className="socialIcons">
            <a href="https://www.facebook.com/djtrovaonl/"><img src={Facebook} alt="Facebook" /></a>
            <a href="https://www.instagram.com/djtrovaonl/"><img src={Instagram} alt="Instagram" /></a>
            <a href="https://soundcloud.com/djtrovaonl"><img src={Soundcloud} alt="Soundcloud" /></a>
            <a href="https://www.youtube.com/channel/UCfCEECZ2Egx1QN9D1BVnhcg"><img src={Youtube} alt="Youtube" /></a>
          {/* <div>Icons made by <a href="https://www.flaticon.com/authors/roundicons" title="Roundicons">Roundicons</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a></div> */}
          </div>
        </div>
      </div>
    );
  }
}

export default Splash;